.login {
  width: 100%;
  height: 100%;
  background-color: #f5f9fe;
  background-size: 100% 100%;
  padding: 200px 0;
}
.login .login-content {
  display: flex;
  width: 1000px;
  height: 400px;
  background-color: white;
  border-radius: 20px;
  margin: 0 auto;
  box-shadow: 0px 0px 80px rgba(0, 0, 0, 0.05);
}
.login .login-content .login-Image {
  width: 50%;
  height: 100%;
  background-image: url(/static/media/login-pancake.7e4f007f.webp);
  background-size: 100% 100%;
  border-radius: 20px 0 0 20px;
}
.login .login-content .login-Info {
  padding: 70px 0 70px 100px;
}
.login .login-content .login-Info h2 {
  font-size: 25px;
  text-align: center;
  color: black;
  margin-bottom: 30px;
}
.login .login-content .login-Info .login-form {
  display: flex;
  flex-direction: column;
}
.login .login-content .login-Info .login-form .login-form-button {
  width: 300px;
  border-radius: 10px;
}
